<template>
  <section>
      <v-row no-gutters class="mx-auto">
        <v-container >
            <v-theme-provider light>
                <v-form  ref="form" v-model="valid" lazy-validation>
                        <v-col cols="12" class="mx-auto" md="8" xsm="12">
                            <v-text-field 
                            v-model="name" 
                            :rules="[rules.required]" 
                            label="Name" 
                            maxlength="20" 
                            required
                            name="name"
                            ></v-text-field>
                        </v-col>
                    
                        <v-col cols="12" class="mx-auto" md="8" xsm="12">
                            <v-text-field 
                            v-model="email" 
                            :rules="emailRules" 
                            label="E-mail" 
                            name="_replyto"
                            required
                            ></v-text-field>
                        </v-col>
                        <v-col cols="12" class="mx-auto" md="8" xsm="12">
                            <v-textarea
                            outlined
                            label="Message"
                            name="message"
                            required
                            v-model="message" 
                            ></v-textarea>
                        </v-col>
                        
                        <v-spacer></v-spacer>

                        <v-col class="d-flex pb-8 mx-auto" cols="12" md="3" xsm="12">
                            <v-btn 
                            x-large 
                            block 
                            :disabled="!valid" 
                            color="indigo white--text" 
                            @click="validate"
                            >Send Message</v-btn>
                        </v-col>
                    
                    </v-form>
            </v-theme-provider>
        </v-container>
    </v-row>
  </section>
</template>

<script>
import axios from 'axios'

export default {
    name: 'ContactForm',
    methods: {
        validate() {
            if (this.$refs.form.validate()) {
                const data = {
                    name: this.name,
                    email: this.email,
                    message: this.message,
                }
                // console.log(data)
                const response = axios.post(this.endpoint, data)
                console.log(response)
                this.$refs.form.reset()
                }
            }
        },
        data: () => ({
            valid: true,
            name: "",
            email: "",
            message: "",
            verify: "",
            endpoint: 'https://formspree.io/f/xgeryoaq',
            emailRules: [
            v => !!v || "Required",
            v => /.+@.+\..+/.test(v) || "E-mail must be valid"
            ],
            rules: {
            required: value => !!value || "Required."
            }
        })
}
</script>

